import React from "react";

const cardControls = React.forwardRef((props, ref) => {
  return (
    <div className="card-controls">
      <div className="additions">
        <div className="image-container">
          <img
            src="#"
            data-src={
              props.data.img
                ? `${props.data.img}`
                : "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSols5HZxlQWyS9JY5d3_L9imbk0LiziHiyDtMZLHt_UNzoYUXs2g"
            }
            alt={props.data.word}
            ref={ref}
          />
        </div>
        <div className="word-context">
          <h4 className="context-title">Context of usage:</h4>
          <p className="context">
            {props.data.usage
              ? props.data.usage
              : "There is no context for this word"}
          </p>
        </div>
        <div className="voice-over" onClick={props.voiceOver}>
          <i>Spell it</i>
        </div>
        <form
          className="controls"
          onClick={props.click}
          onSubmit={e => e.preventDefault()}
        >
          <button className="skip-word">I do not know</button>
          <button className="next-word">Next word</button>
        </form>
      </div>
    </div>
  );
});

export default cardControls;



