import React, { Component } from "react";

import "../../HOC/Themes/Christmas/Christmas.css";

class ProgressBar extends Component {
  render() {
    let { totalWords, doneWords, correctWords } = this.props.props;
    return (
      <div className="progress-panel">
        <form>
          <label>
            <progress
              className="candystick"
              max={totalWords}
              value={doneWords}
            />
            <span className="progress-percentage">
              {~~((correctWords / totalWords) * 100)}% correct
            </span>
          </label>
          <span className="correct-words">
            {correctWords} out of {totalWords} is correct
          </span>
        </form>
      </div>
    );
  }
}

export default ProgressBar;



