import React, {Fragment} from "react";
import Backdrop from "../Backdrop/Backdrop";
import "./Modal.css";

const Modal = props => {
  return (
    <Fragment>
      <Backdrop show={props.show} click={props.click} />
      <div className={props.show ? "Modal -Open" : "Modal -Close"}>
        {props.children}
      </div>
    </Fragment>
  );
};

export default React.memo(Modal);



