import React from "react";
import "./DrawerToggleButton.css";

const DrawerToggleButton = props => {
  return (
    <div className="DrawerToggleButton" onClick={props.click}>
      <div />
      <div />
      <div />
    </div>
  );
};

export default DrawerToggleButton;



