import React from "react";
import "./Input.css";

const input = props => {
  let inputElement = null;
  let errorMessage = null;
  let isValid = "";
  if (props.invalid && props.shouldValidate && props.touched) {
    isValid = " Invalid";
    errorMessage = (
      <p className="validation-error">Please, enter the correct data</p>
    );
  }

  switch (props.elementType) {
    case "input":
      inputElement = (
        <input
          className={"Input" + isValid}
          {...props.data}
          onChange={props.change}
        />
      );
      break;
    case "textarea":
      inputElement = <textarea {...props.data} onChange={props.chane} />;
      break;
    case "select":
      inputElement = (
        <select
          className={"Select" + isValid}
          value={props.value}
          onChange={props.change}
        >
          {props.data.options.map(elem => (
            <option value={elem.value} key={elem.value}>
              {elem.text}
            </option>
          ))}
        </select>
      );
      break;
    default:
      inputElement = (
        <input
          className={"Input" + isValid}
          {...props.data}
          onChange={props.chane}
          value={props.value}
        />
      );
  }

  return (
    <div className="InputContainer">
      <label className="Label">{props.label}</label>
      {inputElement}
      {errorMessage}
    </div>
  );
};

export default input;



