import React, { useState } from "react";
import Logo from "../../Logo/Logo";
import NavigationItems from "../NavigationItems/NavigationItems";
import SideDrawer from "../SideDrawer/SideDrawer";
import DrawerToggleButton from "../SideDrawer/DrawerToggleButton/DrawerToggleButton";
import "./Toolbar.css";

const Toolbar = props => {
  let state = { sideDrawerIsOpen: false };
  let [isOpen, toggle] = useState(state);
  const toggleSideDrawer = () => {
    toggle(prevState => ({ sideDrawerIsOpen: !prevState.sideDrawerIsOpen }));
  };
  
  return (
    <div className="Toolbar">
      <SideDrawer
        isAuth={props.isAuth}
        isOpen={isOpen.sideDrawerIsOpen}
        click={toggleSideDrawer}
      />
      <DrawerToggleButton click={toggleSideDrawer} />
      <Logo />
      <nav>
        <NavigationItems isAuth={props.isAuth} />
      </nav>
    </div>
  );
};

export default Toolbar;



